/*correct tacit css issues for tables*/
h1 {
  margin-top: 0;
  margin-left: 1rem;
}
table {
  width: unset;
}
td,th {
  text-align: center;
}
@media (max-width: 1024px) {
  table {
    width: 100%;
  }
  table thead {
    display: table-header-group;
  }
  table tr {
    display: table-row;
  }
  table tr td,
  table tr th {
    display: table-cell;
  }
}
fieldset {
  margin-left: 1em;
}
